*{
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

a{
  text-decoration: none;
}

.section{
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.section div{
  max-width: 1980px;
}

@media (max-width: 768px){
  .section {
    height: auto;
  }
}